@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@200;400;800;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

::-webkit-scrollbar{
  width: 5px;

}

::-webkit-scrollbar-track{
  background-color: #030518 ;
}


::-webkit-scrollbar-thumb{
  background-color: #25D6DA ;
  border-radius: 20px;
}


body {
  background: #1F1E1F;
  font-family: 'Montserrat',sans-serif;    
}

.App {
  text-align: center;
}



.container-1 {
  display: flex;
  height: 95vh;
  width: 100%;
  margin: 0 auto;
  justify-content: center;
  align-items: center;
}
.container-1 h1{
  color: #d1f7ff
}


.shape {
  z-index: 0;
  -webkit-animation: morph 8s ease-in-out infinite;
          animation: morph 8s ease-in-out infinite;
  border-radius:  60% 40% 30% 70%/60% 30% 70% 40%;
  border: 0.5rem solid #05d9e8;
  height: 30vh;
  transition: ease-in-out 0.5s ;
  width: 15vw;
  
}
.main{
  padding-left: 5vw;
  justify-content: center;
  align-items: center;
    
}

.main h1{
  color: #f1f1f1;
  text-transform: uppercase;
  font-weight: 800;
  font-size: 2rem;
}

.main p{
  color: #818181;
  font-weight: 400;
  font-size: 0.9rem;
  line-height: 2rem;
  
}

.box{
  display: inline-block;
}

.icones{
  padding-right: 1rem;
  font-size: 1.5rem;
  color: #f1f1f1;
}

.icones:hover{
  transition: 0.5s;
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}



.container-2{
  padding-top: 3rem;
  padding-bottom: 3rem;
  margin: 0 auto;
  height: 100%;
  width: 60%;
  text-align: justify;
  justify-content: center;
  display: block;
}


.outdoor{
  height: 50vh;
  width: 30vw;
  padding-left:50% ;
  background: url(/static/media/deskM.adb51e6f.JPG);
  background-position: center;
  background-size: cover ;

}

.content{
  display: flex;
}
.about{
  margin: 0 auto;
  padding-top: 1rem;
}
.about h1{
  color: #f1f1f1;
  font-weight: 500;
  font-size: 4vh;
  
}
.about p{
  width: 25vw;
  color: #818181;
  font-size: 1em;
  font-weight: 400;
  line-height: 2rem;
  
  
}

.about li{
  list-style: none;
  color: #f1f1f1;
  padding-top: 0.5rem;
  font-size: 1em;
  line-height: 2rem;
}

.infos{
  margin-left: 5rem;
  margin-top: 15%;
  width: 70%;
}
.infos span{
  
  color: #818181;
}

.habilidades{
  margin: 0 auto ;
  text-align: center;
}
.habilidades h1{ 
    text-align: left;
    color: #f1f1f1;
    font-weight: 500;
    font-size: 4vh;
}

.habilidades p{
  color: #818181;
  font-size: 1em;
  font-weight: 400;
  line-height: 2rem;
  
}
.box p{
  font-family: 'Montserrat',sans-serif;  
  font-size: 1rem;
  font-weight: 500 ;
  color: #05d9e8;
}
.habilidades .box{
  padding-top: 3rem;
}
.habilidades .icones{
  -webkit-margin-start: 1rem;
          margin-inline-start: 1rem;
  font-size: 4rem;
}


.container-3{
  padding-top: 3rem;
  padding-bottom: 3rem;
  margin: 0 auto;
  height: 100%;
  width: 60%;
  text-align: justify;
  justify-content: center;
  display: block;
}

.container-3 h1{
  text-align: left;
  color: #f1f1f1;
  font-weight: 500;
  font-size: 4vh;
}



.grid-template-columns {
  display: grid;
  padding-top:  5%;
  padding-bottom: 5%;
  grid-gap: 2rem;
  gap: 2rem;
  grid-template-columns: 48% 48%;
  color: white;
}

.grid-template-columns a{
  text-decoration: none;
}

.item {
  margin: 0 auto;
  height: 30vh;
  width: 95%;
  background-size: cover;
  border: 2px solid #e7e7e7;
  border-radius: 5px;
}


.cortai{
  background-image: url(/static/media/Cortai-project.6d1d48de.png);
}

.cmemoria{
  background-image: url(/static/media/Centro-memoria-project.db9f67f2.png);
}

.mais-ifmg{
  background-image: url(/static/media/mais-ifmg-project.2cf39283.png);
}

.autohouse{
  background-image: url(/static/media/autohouse-project.05afc8d4.png);
}


.close {
  color: #aaa;
  float: right;
  font-size: 28px;
  font-weight: bold;
  -webkit-user-select: none ;
      -ms-user-select: none ;
          user-select: none ;
}


.close:hover,
.close:focus {
  color: black;
  text-decoration: none;
  cursor: pointer;
}

.padding{
  margin-top: 2%;
  margin-left: 2.5%;
  width: 95%;
}

.project-container{
  text-align: justify;
  line-height: 2rem;  
  padding-bottom: 1em;
  padding-top: 1em;
  border-radius: 10px;
  border: 1px;
  width: 95%;
  margin: 0 auto;
}

.project-container:hover{
  background: #222222;
  transition: ease-in-out, 0.5s;
  border: 1px solid #F231A5;

}

.project-container h1{
  padding-top: 0.5rem;
  text-align: justify;
  color: #25D6DA;
  font-size: 1.5rem;
  font-weight: bold;
}


.project-container p{
  font-size: 0.8rem;
}

.tags{
  display: flex;
}

.tag{
  padding-left: 1rem;
  padding-right: 1rem;
  font-size: 1rem;
  border: 1px solid white;
  border-radius: 20px;
  font-size: 0.8rem;
  
}
.tag:hover{
  background-color: #F231A5;
  transition: ease-in-out 0.3s;
  cursor: pointer;
}

.footer{
  padding-top: 5rem;
  position: relative;
  display: flex;
  justify-content: space-evenly;
  align-items: center ;
  height: 100%;
  width: 100%;
  margin: 0 auto;
  padding-bottom: 4rem;
  justify-content: center;
  align-items: center;
}

.footer-text{
  text-align: justify;
  align-self: stretch;
  width: 30%;
  color: white;
}


.footer-form{
  padding-top: 2rem;
  padding-bottom: 2rem;
  padding-left: 1.2rem;
  text-align: justify;
  min-height: 300px;
  width: 30%;
  background-color: #030518;
  border-radius: 15px;
  color: white;
}


#contact-form input{
  background-color: #111111;
  margin-top: 1em;
  margin-bottom: 1em;
  padding-left: 1em;
  height: 2rem;
  width: 90%;
  color: white;
  border: 1px solid white;
  border-radius: 7px;
}

#contact-form textarea{
  
  background-color: #111111;
  resize: none;
  width: 90%;
  height: 10em;
  padding-left: 1em;
  padding-top: 1em;
  margin-top: 1em;
  color: white;
  border: 1px solid white;
  border-radius: 7px;
}  

#contact-form .button{
  
  border:none;
  background-color: #F231A5;
  width: 95%;
  margin-bottom: -1em;
}

#contact-form .button:hover{
  background-color: #F09;
  -webkit-transform: scale(1.02);
          transform: scale(1.02);
  transition: ease-in-out 0.3s;
}

.sub-footer{
  background-color: #111111;
  color: white;
  height: 2rem;
  padding-top: 1rem;
  text-align:center; 
  font-size: 0.5;
  
}


@-webkit-keyframes morph {
  0% {
    border-radius: 60% 40% 30% 70%/60% 30% 70% 40%;
    background-image: url(/static/media/me.ea098699.jpg);
    background-position: center;
    background-size: cover;
    
  }
  50% {
    border-radius: 30% 60% 70% 40%/50% 60% 30% 60%;
    background-image: url(/static/media/me.ea098699.jpg);
    background-position: center;
    background-size: cover;
  }
  100% {
    border-radius: 60% 40% 30% 70%/60% 30% 70% 40%;
    background-image: url(/static/media/me.ea098699.jpg);
    background-position: center;
    background-size: cover;
  } 
}


@keyframes morph {
  0% {
    border-radius: 60% 40% 30% 70%/60% 30% 70% 40%;
    background-image: url(/static/media/me.ea098699.jpg);
    background-position: center;
    background-size: cover;
    
  }
  50% {
    border-radius: 30% 60% 70% 40%/50% 60% 30% 60%;
    background-image: url(/static/media/me.ea098699.jpg);
    background-position: center;
    background-size: cover;
  }
  100% {
    border-radius: 60% 40% 30% 70%/60% 30% 70% 40%;
    background-image: url(/static/media/me.ea098699.jpg);
    background-position: center;
    background-size: cover;
  } 
}

@media only screen and (max-width :1024px) {
    .navbar {
        display: inline-block;
        overflow: hidden;
        background-color: #111111;
        position: fixed;
        top: 0;
        height: 3.3rem;
        width: 100%;
        z-index: 5;
    }
    .navbar h1 {
        margin-left: 1em;
        font-size: 1.3rem;
        color: #f1f1f1;
    }
    .toggle-btn {
        display: block;
        position: fixed;
        right: 10px;
        top: 10px;
        cursor: pointer;
        z-index: 5;
    }
    .container-1 {
        padding-top: 25%;
        display: block;
        height: 90vh;
        width: 100%;
        -webkit-user-select: none;
            -ms-user-select: none;
                user-select: none;
    }
    .shape {
        margin: auto;
        display: block;
        height: 30vh;
        width: 60vw;
        flex: 2 1;
    }
    .main {
        margin: 0 auto;
        padding-left: 0;
        padding-top: 5vh;
        display: block;
        justify-content: center;
        align-items: center;
        width: 90%;
    }
    .main h1 {
        text-align: center;
        display: block;
        color: #f1f1f1;
        text-transform: uppercase;
        font-weight: 800;
        font-size: 1.2rem;
    }
    .main p {
        text-align: center;
        color: #818181;
        font-weight: 400;
        font-size: 0.8rem;
        line-height: 1.5rem;
    }
    .box {
        width: 100%;
        text-align: center;
    }
    .container-2 {
        width: 90%;
        padding-top: 0;
    }
    .outdoor {
        width: 50%;
        height: 30vh;
        background-size: cover;
    }
    .about {
        flex: 2 1;
    }
    .content {
        display: block;
    }
    .about h1 {
        display: block;
        color: #f1f1f1;
        font-weight: 500;
        font-size: 4vh;
        text-align: left;
    }
    .about p {
        width: 95%;
        color: #818181;
        font-size: 0.8em;
        font-weight: 400;
        line-height: 1.5rem;
        text-align: justify;
    }
    .about li {
        list-style: none;
        color: #f1f1f1;
        font-size: 0.8em;
    }
    .infos {
        display: block;
        width: 70vw;
        justify-content: center;
        margin-left: -8vw;
        margin-top: -3vh;
        text-align: justify;
    }
    .container-3 {
        width: 92%;
        margin: 0 auto;
    }
    .grid-template-columns {
        display: list-item;
        padding-top: 10vh;
        list-style: none;
        color: #f1f1f1;
        grid-template-columns: 20rem 20rem;
    }
    .item {
        margin-bottom: 25px;
        height: 30vh;
        width: 90%;
        background-size: 100%;
        border: 2px solid #e7e7e7;
        color: #f1f1f1;
        text-align: center;
        font-size: 1.5em;
    }
    .habilidades .icones {
        font-size: 2rem;
    }
    .sidenav {
        display: none;
        padding-left: 10vw;
        padding-top: 25vh;
        height: 100vh;
        width: 100vw;
        transition: 0.5s;
        text-align: center;
        opacity: 0.9;
    }
    .sidenav a {
        font-size: 2rem;
    }
    .sidenav ul {
        text-align: center;
    }
    .project-text {
        width: 80vw;
    }
    .footer {
        padding-top: 5rem;
        display: block;
        justify-content: center;
        align-items: center;
        height: 100%;
        width: 100%;
        margin: 0 auto;
        padding-bottom: 4rem;
    }
    .footer-text {
        margin: auto;
        text-align: center;
        align-self: center;
        width: 90%;
    }
    .footer-form {
        margin: auto;
        width: 90%;
    }
}

@media only screen and (min-width: 150px)and (max-width: 430px) {
    .item {
        height: 20vh;
    }
}

@media only screen and (min-width: 150px)and (max-width: 340px) {
    .tags {
        text-align: center;
        display: block;
    }
    .tag {
        width: 100%;
    }
}
